import TreeTableManyCol from './treeTableManyCol';
import TreeTable from './treeTable';
import useTreeTable from './store';
import {
    initTreeTableData,
    setChildNode,
    editRowEve,
    addChildRowEve,
    addBrotherRow,
    delRowByPk,
    createNewData,
    getAllValue,
    getValueByRowId,
    getExpandedRowKeys,
    showModal,
    emptyAllData,
    openRow,
    closeRow,
    getSelectedRow,
    searchTreeTable,
    updataDataSource,
    setCheckedKeys,
    setSelectedRow,
    updateChildRow,
    treeTableCol,
} from './methods';

let TreeTableAPI = {
    TreeTableManyCol,
    treeTableCol,
    initTreeTableData,
    setChildNode,
    editRowEve,
    addChildRowEve,
    addBrotherRow,
    delRowByPk,
    createNewData,
    getAllValue,
    getValueByRowId,
    getExpandedRowKeys,
    showModal,
    emptyAllData,
    openRow,
    closeRow,
    getSelectedRow,
    searchTreeTable,
    updataDataSource,
    setCheckedKeys,
    setSelectedRow,
    updateChildRow,
};

/**
 * 
 * @param {string} mainCode 
 * @param {object} config 
 */

export default function treeTableCompatible() {
    this.register("treeTable", useTreeTable); //  this.treeTableDataSource[id]
    return { ...apiBindPage.call(this, TreeTableAPI) };
}

function apiBindPage(simpleTableAPI) {
    let res = {}
    for (let pop of Object.keys(simpleTableAPI)) {
        res[pop] = simpleTableAPI[pop].bind(this);
    }
    return res;
}

treeTableCompatible.component = TreeTable;
treeTableCompatible.displayName = "TreeTable";
treeTableCompatible.unfinished = ["treeTableUnFinishedApi"];
