/**
 * @desctabindex = -1,
 * @param tr {node} 
 * @param needJumpTimes {number} /
 * @param direction {string}  'down'/ 'up' 
 * @return /
 */
export default function calculateNeedJumpTimes(tr, needJumpTimes, direction) {
    let tempTr = tr;
    let tempNeedJumpTimes = needJumpTimes;
    while (tempTr !== null && tempTr.getAttribute('tabindex') === '-1') {
        tempNeedJumpTimes += 1;
        tempTr = direction === 'up' ? tempTr.previousElementSibling : tempTr.nextElementSibling;
    }
    return tempNeedJumpTimes;
}
