import getCellIdentity from './getCellIdentity';
import { findNextFocusItemInForm } from './findNextFocusItemInForm';
import { checkboxInTable } from './handleSpecialCase';
import { keyByModuleId } from '../createKeyMap';
import { MODULE_TYPE } from '../config/moduleType';
import { config as CONFIG } from '@platform/api'

/**
 * @desc 
 * @param target {dom} 
 * @param ViewModel {object} 
 * @param moduleId {string} Id
 * @param orderOfHotKey {array} 
 */
function findNextFocusItemInTable({ eventTarget, ViewModel, moduleId, orderOfHotKey }) {
    var parentTd = eventTarget.tagName === 'TD' ? eventTarget : findParentTd(eventTarget);
    if (!parentTd) {
        // keydown
        console.warn('not find parentTd, next hotkey invalid');
        return;
    }
    var nextTd = parentTd.nextElementSibling;
    var currentTr = parentTd.parentElement;
    var lastTd = findLastTdInRow(currentTr);
    var div = nextTd && nextTd.querySelector('[tabindex="0"]');

    //
    if (lastTd === parentTd) {
        var lastTr = currentTr.parentElement.lastElementChild;

        currentTr === lastTr
            ? perceptionHotKeyConfig({ currentModuleId: moduleId, ViewModel, orderOfHotKey })
            : findNextRowTd({ tr: currentTr, ViewModel, moduleId, orderOfHotKey });
        return;
    }

    // 
    if (div === null || checkboxInTable(div)) {
        findNextSiblings({ td: nextTd, ViewModel, moduleId, orderOfHotKey });
        return;
    }
    // table ,id
    let cellIdentity = getCellIdentity(nextTd);
    cellIdentity
        ? ViewModel.enterTriggerCellId = cellIdentity
        : '';
    executeAutoFocus({ div, ViewModel });
}

function findParentTd(el) {
    while (el.parentElement) {
        el = el.parentElement;
        if (el.tagName === 'TD') {
            return el;
        }
    }
    return null;
}

/**
 * @desc td
 * @param tr {dom}
 * @return td {dom}
 */
function findLastTdInRow(tr) {
    let td = tr.lastElementChild.className.includes('fixed')
        ? tr.lastElementChild.previousElementSibling
        : tr.lastElementChild;

    return td;
}

/**
 * @desc 
 * @param td {dom} td
 * @param ViewModel {object} 
 * @param moduleId {string} Id
 * @param orderOfHotKey
 */
function findNextSiblings({ td, ViewModel, moduleId, orderOfHotKey }) {
    var nextTd = td.nextElementSibling;
    var currentTr = nextTd && nextTd.parentElement;
    var lastTd = currentTr && findLastTdInRow(currentTr);
    var currentDiv = td.querySelector('[tabindex="0"]');
    var div = nextTd && nextTd.querySelector('[tabindex="0"]');

    //
    if ((lastTd === nextTd && div === null) || (lastTd === td && currentDiv === null)) {
        findNextRowTd({ tr: currentTr, ViewModel, moduleId, orderOfHotKey });
        return;
    }

    if (div === null || checkboxInTable(div)) {
        findNextSiblings({ td: nextTd, ViewModel, moduleId, orderOfHotKey });
        return;
    }

    let cellIdentity = getCellIdentity(nextTd);
    cellIdentity
        ? ViewModel.enterTriggerCellId = cellIdentity
        : '';
    executeAutoFocus({ div, ViewModel });
}

//
function findNextRowTd({ tr, ViewModel, moduleId, orderOfHotKey }) {
    var nextTr = tr && tr.nextElementSibling;

    //
    if (nextTr === null) {
        orderOfHotKey &&
            jumpToNextModuleFocusItem({ currentModuleId: moduleId, ViewModel, orderOfHotKey });
        return;
    }

    if (nextTr.getAttribute('tabindex') === '-1') {
        findNextRowTd({ tr: nextTr, ViewModel, moduleId, orderOfHotKey });
        return;
    }

    var div = nextTr.querySelector('td [tabindex="0"]');
    var td = findParentTd(div);

    let cellIdentity = getCellIdentity(td);
    cellIdentity
        ? ViewModel.enterTriggerCellId = cellIdentity
        : '';
    executeAutoFocus({ div, ViewModel });
}

/**
 * @desc  orderOfHotKey
 * @param currentModuleId {string} ID
 * @param ViewModel 
 * @param orderOfHotKey {array} 
 */

function perceptionHotKeyConfig({ currentModuleId, ViewModel, orderOfHotKey }) {
    if (orderOfHotKey && orderOfHotKey.length === 0) {
        findFirstFocusItemInCurrentModule({ currentModuleId, ViewModel });
    } else {
        orderOfHotKey &&
            jumpToNextModuleFocusItem({ currentModuleId, ViewModel, orderOfHotKey });
    }
}

function findFirstFocusItemInCurrentModule({ currentModuleId, ViewModel }) {
    let id = `${CONFIG.hotKeyModuleIdPrefix}-${currentModuleId}`;
    let tableTr = document.querySelector(`#${id} table tbody tr`);
    let tds = tableTr.querySelectorAll('td');

    let firstFocusItem = Array.prototype.find.call(tds, function (td) {
        return td.querySelector('[tabindex="0"]') !== null;
    });

    let cellIdentity = getCellIdentity(firstFocusItem);
    cellIdentity
        ? ViewModel.enterTriggerCellId = cellIdentity
        : '';

    var div = firstFocusItem.querySelector('[tabindex="0"]');
    div && executeAutoFocus({ div, ViewModel });
}

function jumpToNextModuleFocusItem({ currentModuleId, ViewModel, orderOfHotKey }) {
    let nextModuleId = calculateNextModuleId(currentModuleId, orderOfHotKey);

    let nextModuleKeyMap = keyByModuleId[nextModuleId];

    if (nextModuleKeyMap === undefined) return;

    let type = nextModuleKeyMap.type;
    if (type === MODULE_TYPE.cardTable || type === MODULE_TYPE.editTable) {
        //tabletable
        let activeTableKeyMap = keyByModuleId[nextModuleKeyMap.activeTable];
        let firstItemDom = document.querySelector(`div[${activeTableKeyMap.firstItem}]`);
        let div = firstItemDom.querySelector('[tabindex="0"]');

        if (div === null) {
            let td = firstItemDom.parentElement;
            findNextSiblings({ td, ViewModel, moduleId: activeTableKeyMap.moduleId, orderOfHotKey });
        } else {
            let td = findParentTd(div);

            /**
       * @desc enter
       * @type {string}
       */
            let cellIdentity = getCellIdentity(td);
            cellIdentity
                ? ViewModel.enterTriggerCellId = cellIdentity
                : '';
            executeAutoFocus({ div, ViewModel });
        }

    } else {
        findNextFocusItemInForm(nextModuleKeyMap, nextModuleId);
    }
}

function calculateNextModuleId(currentModuleId, orderOfHotKey) {
    let orderOfHotKeyLen = orderOfHotKey.length;

    let nextModuleIndex = orderOfHotKey.indexOf(currentModuleId) + 1 === orderOfHotKeyLen
        ? 0
        : orderOfHotKey.indexOf(currentModuleId) + 1;
    return orderOfHotKey[nextModuleIndex];
}

function executeAutoFocus({ div, ViewModel }) {
    if (div !== null) {
        ViewModel.shouldAutoFocus = true;
        div.ncExecuteFocus = true;
        div.focus();
        delete div.ncExecuteFocus;
    }
}

export {
    findNextFocusItemInTable,
    executeAutoFocus,
};
