import { keyByModuleId, orderOfHotKey } from './createKeyMap';
import { MODULE_TYPE } from './config/moduleType';
import { ViewModel, config, PubSub } from '@platform/api'
import { findNextFocusItemInTable } from './util/findNextFocusItemInTable';
import { findPrevFocusItemInTable } from './util/findPrevFoucsItemInTable';
import { findUpStairsFocusItemInTable } from './util/findUpStairsFocusItemInTable';
import { findDownStairsFocusItemInTable } from './util/findDownStairsFocusItemInTable';

const ARROW_KEY = {
    37: handleLeftArrow,
    38: handleUpArrow,
    39: handleRightArrow,
    40: handleDownArrow,
};

/**
 *
 * @param moduleId { String } moduleId
 * @param e { Object } 
 * @param key  { String } 
 */

function keyDownOfShiftUp(moduleId, e, key) {
    let nextFocus = key === 'shift+down' ? +1 : -1;

    let nextFocusModuleIdIndex = orderOfHotKey.indexOf(moduleId) + nextFocus;
    let nextFocusModuleId = orderOfHotKey[nextFocusModuleIdIndex];

    if (e.target) { // 
        let code = null;
        // item item
        // if (keyByModuleId[moduleId][item]) {
        //     code = keyByModuleId[moduleId][item];
        // }

        if (!isLastModule(nextFocusModuleId) && keyByModuleId[nextFocusModuleId].firstItem) {
            code = keyByModuleId[nextFocusModuleId].firstItem;
        }
        let wrapperDom = document.querySelector(`div[field=${code}]`);
        if (!wrapperDom || !(wrapperDom && wrapperDom.contains(e.target))) {
            e.target.blur();
        }
    }

    if (isLastModule(nextFocusModuleId)) {
        focusInSelfModule({ key, moduleId });
        return;
    }

    //
    if (nextItemNotHaveData(this.props, moduleId, nextFocusModuleId)) {
        keyDownOfShiftUp.call(this, nextFocusModuleId, e, key);
        return;
    }

    PubSub.publish('autoFocus', {
        data: keyByModuleId[nextFocusModuleId].firstItem,
        moduleId,
    });
}

/**
 * @desc keyByModuleId nextFocusModuleId moduleId
 * @param nextFocusModuleId {string}
 * @return {boolean}
 */
function isLastModule(nextFocusModuleId) {
    return keyByModuleId[nextFocusModuleId] === undefined;
}

function focusInSelfModule({ key, moduleId }) {
    let item = key === 'shift+down' ? 'lastItem' : 'firstItem';

    //tab
    if (hasTabRelation(moduleId)) {
        activeItemFocus(moduleId, item);
        return;
    }

    PubSub.publish('autoFocus', {
        data: keyByModuleId[moduleId][item],
        moduleId,
    });
}

// 
function hasTabRelation(moduleId) {
    let moduleInfo = keyByModuleId[moduleId];
    return moduleInfo && (moduleInfo.parent || moduleInfo.children);
}

/**
 * @desc: 
 * @param: moduleId {string} Id
 * @param: item {string} 
 */
function activeItemFocus(moduleId, item) {
    //undefined ID
    let activeKey = ViewModel.tableActiveKey || moduleId;

    PubSub.publish('autoFocus', {
        data: keyByModuleId[activeKey][item],
        moduleId,
    });
}

/**
 * @desc moduleId,keyDownOfShiftUp,
 * @param props {object}
 * @param currentModuleId {string} ID
 * @param nextModuleId {string} ID
 */
function nextItemNotHaveData(props, currentModuleId, nextModuleId) {
    const currentModuleType = keyByModuleId[currentModuleId].type;
    const nextModuleType = keyByModuleId[nextModuleId].type;
    let data = [];

    //type: {form, cardTable, editTable}
    if (currentModuleType === MODULE_TYPE.form) {
        data = getDataByFormDataStructure(props, nextModuleType, nextModuleId);
    } else if (currentModuleType === MODULE_TYPE.cardTable) {
        data = getDataByCardTableDataStructure(props, nextModuleType, nextModuleId);
    } else if (currentModuleType === MODULE_TYPE.editTable) {
        data = getDataByCardTableDataStructure(props, nextModuleType, nextModuleId);
    }

    return data.length === 0;
}
/**
 * 
 * TODO
 * @desc form, table 
 * @param props
 * @param nextModuleType
 * @param nextModuleId
 */
function getDataByFormDataStructure(props, nextModuleType, nextModuleId) {
    let data;
    let myTable = props._this.myTable;
    if (nextModuleType === MODULE_TYPE.cardTable) {
        data = myTable[nextModuleId].state.table.rows;
    } else if (nextModuleType === MODULE_TYPE.editTable) {
        data = myTable.myEditTable[nextModuleId].state.table.rows;
    } else {
        data = props._this.state.meta[nextModuleId].items;
    }

    return data;
}

// TODO
function getDataByCardTableDataStructure(props, nextModuleType, nextModuleId) {
    let data;
    let myTable = props.pageScope.myTable;
    if (nextModuleType === MODULE_TYPE.cardTable) {
        data = myTable[nextModuleId].state.table.rows;
    } else if (nextModuleType === MODULE_TYPE.editTable) {
        data = myTable.myEditTable[nextModuleId].state.table.rows;
    } else {
        data = props.pageScope.state.meta[nextModuleId].items;
    }

    return data;
}

/**
 * @desc: 
 * @param eventTarget {dom} 
 * @param ViewModel {object} 
 * @param moduleId {string} ID
 * @param orderOfHotKey {array} 
 * @param itemType {string}  input/datePicker
 * @param keyCode {number} 
 */
function handleArrowKeyDown({ eventTarget, ViewModel, moduleId, orderOfHotKey, itemType, keyCode }) {
    ARROW_KEY[keyCode]({ eventTarget, ViewModel, moduleId, orderOfHotKey, itemType });
}

/**
 * @desc 
 * @param eventTarget
 * @param ViewModel
 * @param moduleId
 * @param orderOfHotKey
 * @param itemType
 */
function handleLeftArrow({ eventTarget, ViewModel, moduleId, orderOfHotKey, itemType }) {
    //eventTargetdocumenttd,
    if (itemType === config.hotKeyDatePicker) {
        findPrevFocusItemInTable({ eventTarget, ViewModel, moduleId, orderOfHotKey });
        return;
    }

    //CLOUD-147579 enterenter
    let timer = setTimeout(() => {
        findPrevFocusItemInTable({ eventTarget, ViewModel, moduleId });
        clearTimeout(timer);
    });
}

function handleUpArrow({ eventTarget, ViewModel, moduleId, orderOfHotKey, itemType }) {
    //eventTargetdocumenttd,
    if (itemType === config.hotKeyDatePicker) {
        findUpStairsFocusItemInTable({ eventTarget, ViewModel, moduleId, orderOfHotKey });
        return;
    }

    //CLOUD-147579 enterenter
    let timer = setTimeout(() => {
        findUpStairsFocusItemInTable({ eventTarget, ViewModel, moduleId, orderOfHotKey });
        clearTimeout(timer);
    });
}

function handleRightArrow({ eventTarget, ViewModel, moduleId, orderOfHotKey, itemType }) {
    //eventTargetdocumenttd,
    if (itemType === config.hotKeyDatePicker) {
        findNextFocusItemInTable({ eventTarget, ViewModel, moduleId, orderOfHotKey });
        return;
    }

    //CLOUD-147579 enterenter
    let timer = setTimeout(() => {
        findNextFocusItemInTable({ eventTarget, ViewModel, moduleId });
        clearTimeout(timer);
    });
}

function handleDownArrow({ eventTarget, ViewModel, moduleId, orderOfHotKey, itemType }) {
    //eventTargetdocumenttd,
    if (itemType === config.hotKeyDatePicker) {
        findDownStairsFocusItemInTable({ eventTarget, ViewModel, moduleId, orderOfHotKey });
        return;
    }

    //CLOUD-147579 enterenter
    let timer = setTimeout(() => {
        findDownStairsFocusItemInTable({ eventTarget, ViewModel, moduleId, orderOfHotKey });
        clearTimeout(timer);
    });
}

export { keyDownOfShiftUp, handleArrowKeyDown };
