import getCellIdentity from './getCellIdentity';
import { findParentTd } from '@platform/api';
import { executeAutoFocus } from './findNextFocusItemInTable';
import calculateNeedJumpTimes from './calculateNeedJumpTimes';

/**
 * @desc 
 * @param target {dom} 
 * @param ViewModel {object} 
 * @param moduleId {string} Id
 * @param orderOfHotKey {array} 
 */
function findUpStairsFocusItemInTable({ eventTarget, ViewModel, moduleId, orderOfHotKey }) {
    let parentTd = eventTarget.tagName === 'TD' ? eventTarget : findParentTd(eventTarget);
    let upStairTr = parentTd.parentElement.previousElementSibling;
    let jumpedLines = 1; //
    if (upStairTr === null) return;

    if (upStairTr.getAttribute('tabindex') === '-1') {
        jumpedLines = calculateNeedJumpTimes(upStairTr, jumpedLines, 'up');
    }

    let cellIdentity = getCellIdentity(parentTd);

    if (!cellIdentity) {
        return console.warn('not find cell identity!');
    }

    let cellIdentityArray = cellIdentity && cellIdentity.split('-');
    let upStairsLine = Number(cellIdentityArray.pop()) - jumpedLines;

    if (upStairsLine > -1) {
        cellIdentityArray.push(upStairsLine);
        let upStairsCellIdentity = cellIdentityArray.join('-');
        ViewModel.upStairsCellIdentity = cellIdentityArray;

        let div = document.querySelector(`div[${upStairsCellIdentity}="${upStairsCellIdentity}"] [tabindex="0"], div[${upStairsCellIdentity}="${upStairsCellIdentity}"][tabindex="0"]`);

        executeAutoFocus({ div, ViewModel });
    }
}
export {
    findUpStairsFocusItemInTable,
};
