import BillInfo from './BillInfo';
//
/**
 * 
 * @param {*} events : {showBackBtn:true,billCodeText:''}
 */
export function createBillHeadInfo(events) {
    return (
        <BillInfo
            getThis={(_this) => {
                this.myBillInfo = _this;
            }}
            events={events}
            output={this.output}
            rootprops={this.props}
        />
    );
}