import React, { Component } from 'react';
import { HotKeys } from 'react-hotkeys';

import { createByModuleType, deleteKeyByModuleId } from './createKeyMap';
import { stopFormHotKeyChange } from './config/stopHotKeyChange';
import { keyDownOfShiftUp } from './handleKeyDown';
import { keyMap } from './config/tableKeyboardMap';
import { config as CONFIG, PubSub } from '@platform/api';
import './hotKey.less';

function FormWithKeyboardChange(WrappedComponent) {
    return class WithHotKey extends Component {
        constructor(props) {
            super(props);
            this.formStatusChanged = this.formStatusChanged.bind(this);
        }

        componentDidMount() {
            //
            this.autoFocusToken = PubSub.subscribe('autoFocus-IoC', () => {
                //form  add, edit
                let status = this.props.store.getStatus();

                // autoFocusDeferred false
                let autoFocusDeferred = false;

                if (stopFormHotKeyChange({ props: this.props, autoFocusDeferred, status }) || status === 'browse') return;
                createByModuleType['form'](this.props, true);
            });
        }

        componentWillUnmount() {
            // KeyByModuleId   
            deleteKeyByModuleId(this.props.moduleId || this.props.name);
            this.autoFocusToken && PubSub.subscribe(this.autoFocusToken);
        }

        formStatusChanged(props, status) {
            if (status === 'browse') {
                deleteKeyByModuleId(props.moduleId || props.name);
            } else {
                this.judgeCreateKeyMap(props, status);
            }

        }

        judgeCreateKeyMap(props, status) {
            let { autoFocusDeferred = false } = props.getHotKeyConfig ? props.getHotKeyConfig() : {};
            if (stopFormHotKeyChange({ props, autoFocusDeferred, status })) return;
            createByModuleType['form'](props);
        }

        render() {
            const moduleId = this.props.name;
            return (
                <HotKeys
                    keyMap={keyMap}
                    handlers={{
                        shiftUp: keyDownOfShiftUp.bind(this, moduleId),
                        shiftDown: keyDownOfShiftUp.bind(this, moduleId),
                    }}
                    id={`${CONFIG.hotKeyModuleIdPrefix}-${moduleId}`}
                >
                    <WrappedComponent
                        {...this.props}
                        formStatusChanged={this.formStatusChanged}
                    />
                </HotKeys>
            );
        }
    };
}

export default FormWithKeyboardChange;