import React from 'react';
require('./index.less');
import CardPagination from './CardPag_test';

//
export function createCardPagination(events) {
    return (
        <CardPagination
            getThis={_this => {
                this.myCardPagination = _this;
            }}
            events={events}
            output={this.output}
            rootprops={this.props}
            getUrlParam={this.getUrlParam}
        />
    );
}

