// 
function createNewData(val){
    if (isArray(val)) {
        val.map((item, index) => {
            item.key = item.refpk;
            if (!item.iconBox) {
                if (item.key) {
                    item.iconBox = {
                        addIcon: true,
                        delIcon: true,
                        editIcon: true,
                    };
                }
            }
            if (item.key === undefined) {
                console.error('createNewDatarefpk');
                return false;
            }
        });
    } else if (val.refpk) {
        val.key = val.refpk;
        if (!val.iconBox) {
            val.iconBox = {
                addIcon: true,
                delIcon: true,
                editIcon: true,
            };
        }
    } else {
        console.error('createNewData, ');
    }
    return val;
};

//    
function getNewTreeData(treeData, parentKey, child) {
    if (!child || !isArray(child) || child.length === 0) {
        return false;
    }
    const loop = data => {
        if (!data || data.length === 0) {
            return false;
        }
        for(let item of data){
            if (item.key === child[0].pid) {
                item.children = child;
                return;
            } 
             if (item.children) {
               loop(item.children);
            }
        }
    };
    loop(treeData);
    return treeData;
}



// 
function addTreeNode(treeData, data) {
    let pid = data.pid;
    if (pid) {
        let len = treeData.length;
        for (let i = 0; i < len; i++) {
            let node = treeData[i];
            if (node.refpk === pid) {
                if (node.children) {
                    node.children.push(data);
                } else {
                    node.children = [data];
                }
                return true;
            } else if (Array.isArray(node.children)) {
                let res = addTreeNode(node.children, data);
                if (res) {
                    return false;
                }
            }
        }
    } else {
        if (data.refpk) {
            treeData.push(data);
        } else {
            console.warn('addTreeNodedata');
        }
    }
}

