export let staticData = {
    setLangJson(json) {
        langJson = json || [];
    },
    getData: function(type, multi) {
        if (type == "tree" && multi) {
            type = "treeMulti";
        }
        if (type == "grid" && multi) {
            type = "gridMulti";
        }
        if (type == "gridTree" && multi) {
            type = "gridTreeMulti";
        }
        return getData(type);
    }
};
