import React, { Component } from 'react';
import { PubSub } from '@platform/api';
import { matchItemFocus } from './executeAutoFocus';

function SearchWithAutoFocus(WrappedComponent) {
    return class WithAutoFocus extends Component {
        state = { isPass: true }

        componentDidMount() {
            let { areaId, name } = this.props;
            this.searchRequiredToken = PubSub.subscribe('searchRequired', (msg, data) => {
                let selector = '';
                if (Array.isArray(data) && data.includes(name)) {
                    // itemtype normal matchItemFocus
                    this.setState({ isPass: false })
                    if (name === data[0]) {
                        selector = `${areaId ? ('#' + areaId) : ''} [hotkeytag="searchArea-${data[0]}"]`;
                        // console.log(selector);
                        matchItemFocus[this.props.itemtype] === undefined
                            ? matchItemFocus['normal'](selector)
                            : matchItemFocus[this.props.itemtype](selector);
                    }
                } else if (data === name) {
                    this.setState({ isPass: false });
                    selector = `${areaId ? ('#' + areaId) : ''} [hotkeytag="searchArea-${data}"]`;
                    // console.log(selector);
                    matchItemFocus[this.props.itemtype] === undefined
                        ? matchItemFocus['normal'](selector)
                        : matchItemFocus[this.props.itemtype](selector);
                }
            });
            this.clearWarningToken = PubSub.subscribe('clearWarning', (msg, data) => {
                if (Array.isArray(data) && data.includes(name)) {
                    this.setState({ isPass: true })
                } else if (name == data) {
                    this.setState({ isPass: true })
                }
            })
        }

        componentWillUnmount() {
            this.searchRequiredToken && PubSub.subscribe(this.searchRequiredToken);
            this.clearWarningToken && PubSub.subscribe(this.clearWarningToken);
            this.dom = null;
        }

        render() {
            return (
                <WrappedComponent
                    ref={dom => {
                        this.dom = dom;
                    }}
                    {...this.props}
                    isPass={this.state.isPass}
                />
            );
        }
    };
}

export default SearchWithAutoFocus;