const FORM_BROWSE = 'browse';
/**
 * @desc 
 * @param props {object}
 * @param autoFocusDeferred {boolean} true ->  false -> 
 * @param status {string} form
 * @returns {boolean}
 */
function stopFormHotKeyChange({ props, autoFocusDeferred, status }) {
    let { name } = props;
    let { withHotKeyChange, meta } = props.getHotKeyConfig ? props.getHotKeyConfig() : {};

    if (!props.getHotKeyConfig) {
        return console.warn('some form hotkey invalid');
    }

    let moduleType = meta[name] &&
        meta[name]['moduletype'];

    let formStatus;
    if (status) {
        formStatus = status;
    } else {
        //metaname  (metametastatus) metaname
        formStatus = meta[name] ? meta[name].status || FORM_BROWSE : FORM_BROWSE;
    }

    if (name === undefined || withHotKeyChange === false || moduleType === undefined || autoFocusDeferred || formStatus === FORM_BROWSE) {
        return true;
    }

    return false;
}

function stopCardTableHotKeyChange(props) {
    let { name, high_hotkey, getHotKeyConfig } = props;
    // console.log(high_hotkey, getHotKeyConfig)
    let { withHotKeyChange, meta } = props.getHotKeyConfig ? props.getHotKeyConfig() : {};
    if (!props.getHotKeyConfig) {
        return console.warn('some form hotkey invalid');
    }
    let { store, tableId } = high_hotkey;
    if (!store) return true;

    let notEditStatus = store.getTableProps('status') !== 'edit';
    let rows = store.getData();
    let columns = meta[name] && meta[name].items;

    rows = rows === undefined ? [] : rows;

    //rows, withHotKeyChange->falsekeyMap
    return (notEditStatus || rows.length === 0 || !withHotKeyChange || columns === undefined);
}

function stopEditTableHotKeyChange(props, autoFocusDeferred) {
    let { name, high_hotkey, getHotKeyConfig } = props;
    // console.log(high_hotkey, getHotKeyConfig)
    let { withHotKeyChange, meta } = props.getHotKeyConfig ? props.getHotKeyConfig() : {};
    if (!props.getHotKeyConfig) {
        return console.warn('some form hotkey invalid');
    }
    let { store, tableId } = high_hotkey;
    if (!store) return true;

    let notEditStatus = store.getTableProps('status') !== 'edit';
    let rows = store.getData();
    let columns = meta[name] && meta[name].items;

    rows = rows === undefined ? [] : rows;

    //autoFocusDeferred == true || rows withHotKeyChange->falsekeyMap
    return (autoFocusDeferred || notEditStatus || rows.length === 0 || !withHotKeyChange || columns === undefined);

}

export {
    stopFormHotKeyChange,
    stopCardTableHotKeyChange,
    stopEditTableHotKeyChange,
};
