// 
export function getTreeData(id) {
    let store = existenceCheck.call(this, id);
    if (!store) {
        return;
    }
    let treeData = store.getStore('treeData');
    return JSON.parse(JSON.stringify(treeData || [], (key, value) => {
        if (key == 'beforeName' || key == 'afterName') {
            return undefined;
        }
        return value;
    }));
}

