import React, { Component } from 'react';
import { ViewModel, config as CONFIG, PubSub } from '@platform/api'

function TableWithAutoFocus(WrappedComponent) {
    return class WithAutoFocus extends Component {
        componentDidMount() {
            let { store } = this.props;
            this.autoFocusToken = PubSub.subscribe('autoFocus', (msg, { data, moduleId, force }) => {
                let el = document.querySelector(`div[${data}="${data}"] [tabindex="0"]`);
                let activeEl = document.activeElement;
                let tableId = `${CONFIG.hotKeyModuleIdPrefix}-${moduleId}`;
                console.log(tableId, activeEl, store);
                if (WithAutoFocus.judgeFocus(el, activeEl, tableId, force)) {
                    ViewModel.shouldAutoFocus = true;
                    el.ncExecuteFocus = true;

                    // 
                    let cellselectorkey = el.getAttribute('cellselectorkey') || '';
                    cellselectorkey = cellselectorkey.split('@') || [];
                    store && cellselectorkey[0] && cellselectorkey[1] && store.setCellProps(cellselectorkey[0], cellselectorkey[1], { autoFocus: true }, false);

                    //
                    !el.contains(activeEl) && el.focus();
                    delete el.ncExecuteFocus;
                    //   ie
                    setTimeout(() => {
                        ViewModel.shouldAutoFocus = false;
                    }, 100);
                }
            });
        }

        componentWillUnmount() {
            this.autoFocusToken && PubSub.subscribe(this.autoFocusToken);
        }

        /**
         * @desc: addRowsetStatustable
         *        
         * @resolve: tabletabIndex-1autoFocus
         *           tableinputfocus
         * @param el 
         * @param activeEl 
         * @param tableId Id
         * @param force true -> 
         * @return {boolean}
         */
        static judgeFocus(el, activeEl, tableId, force) {
            let tableEL = document.getElementById(tableId);
            if (el) {
                return activeEl.tabIndex !== -1 && tableEL && !tableEL.contains(activeEl) || force;
            }
        }

        render() {
            return (
                <div
                    id={`${CONFIG.hotKeyModuleIdPrefix}-${this.props.tableId || this.props.name}`}
                >
                    <WrappedComponent
                        {...this.props}
                    />
                </div>

            );
        }
    };
}

export default TableWithAutoFocus;
