import React, { Component } from 'react';
import {
    Modal,
    Button,
    HotKeys,
    Tooltip,
} from '@platform/base';
import { getMultiLang } from '@platform/api';

require('./createModal.less');

class Modal2 extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: {},
            json: {},
        };
    }

    UNSAFE_componentWillMount() {
        let callback = (json, bool) => {
            if (bool) {
                this.setState({ json });
            }
        };
        getMultiLang({ moduleId: 'container_toast', callback });
    }

    UNSAFE_componentWillReceiveProps(nextProps) {
        let { modalData, argusObj } = nextProps;
        let nowData = {};
        for (let attr in modalData) {
            for (let attr1 in argusObj) {
                if (attr === attr1) {
                    if (modalData[attr] && argusObj[attr1]) {
                        nowData[attr] = modalData[attr];
                    } else {
                        if (modalData[attr]) {
                            nowData[attr] = modalData[attr];
                        } else {
                            nowData[attr] = argusObj[attr1];
                        }
                    }
                }
            }
        }
        this.setState({ data: nowData });
    }

    render() {
        let { json } = this.state;
        let {
            id,
            size,
            className,
            backdropClassName,
            title,
            content,
            noFooter,
            leftBtnName = json['0005'],
            rightBtnName = json['0006'],
            showCustomBtns,
            customBtns,
            hideRightBtn,
            hideLeftBtn,
            container,
        } = this.state.data;
        let {
            onHide,
            backdrop,
            indexInfo,
            beSureClick,
            cancelClick,
            show,
            size: propSize,
            hotKeyboard = true,
            onEscapeKeyUp,
            draggable,
            resizable,
            ...others
        } = this.props;
        let mSize = size || propSize || '';

        let maxModalCls = mSize === 'max' ? 'nc-modal-max' : '';
        return (
            <Modal
                fieldid={id}
                show={show}
                onHide={onHide.bind(this)}
                size={mSize}
                backdrop={backdrop}
                indexInfo={indexInfo}
                className={`${className} simpleModal ${maxModalCls}`}
                backdropClassName={backdropClassName}
                container={container}
                ref={Modal => (this.Modal = Modal)}
                onEscapeKeyUp={onEscapeKeyUp}
                draggable={draggable}
                resizable={resizable}
                id={id}
                {...others}
            >
                {!showCustomBtns && hotKeyboard && !noFooter && (
                    <HotKeys
                        keyMap={{
                            sureBtnHandler:
                                HotKeys.USUAL_KEYS.MODAL_CONFIRM,
                            cancelBtnHandler:
                                HotKeys.USUAL_KEYS.MODAL_CALCEL,
                        }}
                        handlers={{
                            sureBtnHandler: () => {
                                //  top by bbqin
                                if (this.Modal && this.Modal.isTopModal()) {
                                    console.log(
                                        'createModal  sureBtnHandler ',
                                        this.Modal.isTopModal(),
                                    );
                                    beSureClick.bind(this)();
                                }
                            },
                            cancelBtnHandler: () => {
                                //  top  by bbqin
                                if (this.Modal && this.Modal.isTopModal()) {
                                    console.log(
                                        'createModal cancelBtnHandler ',
                                    );
                                    cancelClick.bind(this)();
                                }
                            },
                        }}
                        className="simpleModal-hotkeys-wrapper"
                        focused={true}
                        attach={document.body}
                        display="inline"
                    />
                )}
                <Modal.Header fieldid="header-area" closeButton>
                    <Modal.Title fieldid={title}>{title}</Modal.Title>
                </Modal.Header>
                <Modal.Body className={noFooter ? 'has-resize-icon' : ''}>{content}</Modal.Body>

                {!noFooter ? (
                    <Modal.Footer>
                        {!showCustomBtns &&
                            hideLeftBtn === false &&
                            leftBtnName &&
                            (hotKeyboard ?
                                (<Tooltip
                                    placement="top"
                                    inverse
                                    overlay={`${leftBtnName}  (${HotKeys.USUAL_KEYS.MODAL_CONFIRM})`}
                                    trigger={['hover', 'focus']}
                                    className="model-helper-overlay"
                                >
                                    <Button
                                        onClick={beSureClick.bind(this)}
                                        colors="primary"
                                        tabIndex="0"
                                        fieldid="confirm"
                                    >
                                        {leftBtnName}(<span className="text-decoration-underline">Y</span>)
                                    </Button>
                                </Tooltip>
                                ) : (<Button
                                    onClick={beSureClick.bind(this)}
                                    colors="primary"
                                    tabIndex="0"
                                    fieldid="confirm"
                                >
                                    {leftBtnName}
                                </Button>))
                        }

                        {!showCustomBtns &&
                            hideRightBtn === false &&
                            rightBtnName &&
                            (hotKeyboard ?
                                (<Tooltip
                                    placement="top"
                                    inverse
                                    overlay={`${rightBtnName}  (${HotKeys.USUAL_KEYS.MODAL_CALCEL})`}
                                    trigger={['focus', 'hover']}
                                    className="model-helper-overlay"
                                >
                                    <Button
                                        onClick={cancelClick.bind(this)}
                                        shape="border"
                                        tabIndex="0"
                                        fieldid="cancel"
                                    >
                                        {rightBtnName}(<span className="text-decoration-underline">N</span>)
                                    </Button>
                                </Tooltip>
                                ) : (
                                    <Button
                                        onClick={cancelClick.bind(this)}
                                        shape="border"
                                        tabIndex="0"
                                        fieldid="cancel"
                                    >
                                        {rightBtnName}
                                    </Button>
                                )
                            )
                        }
                        {showCustomBtns && customBtns ? customBtns : null}
                    </Modal.Footer>
                ) : null}
            </Modal>
        );
    }
}

export default function createModal(
    id,
    {
        title, //
        content, //
        beSureBtnClick, //
        cancelBtnClick, //
        closeModalEve, //
        rightBtnName, //
        leftBtnName, //
        noFooter = false, //
        size = 'lg', //   sm/lg/xlg
        userControl = false, //  / 
        className = '',
        backdropClassName = '',
        zIndex = 200,
        hasBackDrop = true, //
        closeByClickBackDrop = false, //truefalse,false --UE
        showCustomBtns = false, //
        customBtns, //
        validateCloseBtn = false, //X
        hotKeyboard = true, // 
        hideRightBtn = false, //
        hideLeftBtn = false, //
        container = document.body,
        onEscapeKeyUp,
        draggable = true, //
        resizable = true, //
        ...others
    } = {},
) {
    let custom = (className.indexOf('junior') !== -1) || (className.indexOf('senior') !== -1) || (className.indexOf('combine') !== -1);
    let mSize = `${custom ? '' : size}`;
    let argusObj = {
        id,
        title,
        content,
        beSureBtnClick,
        cancelBtnClick,
        closeModalEve,
        rightBtnName,
        leftBtnName,
        noFooter,
        size: mSize, //size
        userControl,
        className,
        backdropClassName,
        zIndex,
        hasBackDrop,
        closeByClickBackDrop,
        showCustomBtns,
        customBtns,
        validateCloseBtn,
        hideRightBtn,
        hideLeftBtn,
        container,
        onEscapeKeyUp,
    };
    if (!this.state.modal.hasOwnProperty(id)) {
        this.state.modal[id] = argusObj;
    }
    let modalData = this.state.modal[id] || {};
    if (!modalData.update) {
        modalData.content = content;
        modalData.customBtns = customBtns;
    }
    //
    const close = () => {
        modalData.showModal = false;
        this.setState({ modal: this.state.modal });
    };
    /*  */
    const onHide = () => {
        if (!modalData.validateCloseBtn) {
            close();
        }
        if (typeof modalData.closeModalEve === 'function') {
            modalData.closeModalEve();
        }
    };

    /*  */
    const cancelClick = () => {
        if (!modalData.userControl) {
            close();
        }
        if (typeof modalData.cancelBtnClick === 'function') {
            modalData.cancelBtnClick();
        }
    };

    //
    const beSureClick = () => {
        if (!modalData.userControl) {
            close();
        }
        if (typeof modalData.beSureBtnClick === 'function') {
            modalData.beSureBtnClick();
        }
    };

    let backdrop;
    if (hasBackDrop) {
        if (closeByClickBackDrop) {
            backdrop = closeByClickBackDrop;
        } else {
            backdrop = 'static';
        }
    } else {
        backdrop = hasBackDrop;
    }
    let finalIndex = modalData.zIndex || zIndex || 200
    let indexInfo = { id, zIndex:finalIndex };
    return (
        <Modal2
            {...others}
            onHide={onHide}
            backdrop={backdrop}
            indexInfo={indexInfo}
            beSureClick={beSureClick}
            cancelClick={cancelClick}
            modalData={modalData}
            show={modalData.showModal}
            argusObj={argusObj}
            hotKeyboard={hotKeyboard}
            custom={custom}
            onEscapeKeyUp={onEscapeKeyUp}
            draggable={draggable}
            resizable={resizable}
            size={mSize}
        />
    );
}
