import React, { Component } from 'react';
import { PubSub } from '@platform/api';
import { matchItemFocus } from './executeAutoFocus';

function FormWithAutoFocus(WrappedComponent) {
    return class WithAutoFocus extends Component {
        componentDidMount() {
            let { name, form } = this.props;

            this.autoFocusToken = PubSub.subscribe('autoFocus', (msg, { data, id }) => {
                let selector = `[hotkeytag="${name}-${data}"]`;
                let itemtype = 'normal';
                if (form && data) {
                    form.setItemAttribute(data, 'autoFocus', true);
                    itemtype = form.getItemAttribute(data, 'itemtype');
                }
                console.log(selector, form, id, data, itemtype);

                // 
                if (id) {
                    if (name === id) {
                        console.log(id)
                        // itemtype normal matchItemFocus
                        matchItemFocus[itemtype] === undefined
                            ? matchItemFocus['normal'](selector)
                            : matchItemFocus[itemtype](selector);
                    }
                } else {
                    if (data) {
                        // itemtype normal matchItemFocus
                        matchItemFocus[itemtype] === undefined
                            ? matchItemFocus['normal'](selector)
                            : matchItemFocus[itemtype](selector);
                    }
                }
            });
        }

        componentWillUnmount() {
            // this.dom = null;
            this.autoFocusToken && PubSub.subscribe(this.autoFocusToken);
        }

        render() {
            // console.log(this.props.children);
            return (
                <WrappedComponent
                    // ref={dom => {
                    //     console.log(dom);
                    //     this.dom = dom;
                    // }}
                    {...this.props}
                />
            );
        }
    };
}

export default FormWithAutoFocus;