
import { availableFormItems, availableTableItems } from './config/availableItems';
import { MODULE_TYPE } from './config/moduleType';
import { config as CONFIG, PubSub } from '@platform/api';

/**
 * 
 *
 * @keyByModuleId = {
 *     moduleId1: {
 *         firstItem: 'attrCode1',
 *         lastItem: 'attrCode2',
 *         type: 'form'
 *     },
 *     moduleId2: {
 *         firstItem: 'attrCode3',
 *         lastItem: 'attrCode4',
 *         type: 'table',
 *         children: ['moduleId3', 'moduleId4'] // 
 *     },
 *     moduleId3: {
 *         firstItem: 'attrCode3',
 *         lastItem: 'attrCode4',
 *         type: 'table',
 *         parent: 'moduleId3', // 
 *         activeTable: 'moduleId3' //
 *                                    
 *     }
 * }
 *
 * @formDisableAttrCode = {
 *     moduleId: {
 *         attrCode1: true,
 *         attrCode2: false
 *     }
 * }
 */
let keyByModuleId = {};
let formDisableAttrCode = {};
let tableDisabledAttrCode = {};
let orderOfHotKey = [];
const hotKeyPrefix = CONFIG.hotKeyModuleIdPrefix;

/**
 * @desc 
 * @param props
 * @param force executeAutoFocus false
 */
function keyMapOfForm(props, force = false) {
    let { name } = props;
    let { meta, orderOfHotKey: newOrderOfHotKey } = props.getHotKeyConfig ? props.getHotKeyConfig() : {};

    //   TOOD
    if (!props.getHotKeyConfig) {
        return console.warn('some form hotkey invalid');
    }

    let formItems = meta[name]['items'];
    orderOfHotKey = newOrderOfHotKey;

    let itemsAttrCode = formItems.filter(item => {
        return isAvailableFormItem(item, name);
    }).map(item => {
        return item.attrcode;
    });

    if (formAutoFocusJudgement(name, force)) {
        keyByModuleId[name] = {
            firstItem: itemsAttrCode[0],
            type: MODULE_TYPE.form,
        };
        let firstItemKey = itemsAttrCode[0];
        PubSub.publish('autoFocus', { data: firstItemKey });
    }
}

/**
 * @desckeyMapOfFormform
 * meta
 * keyByModuleIdmoduleId
 */
function formAutoFocusJudgement(name, force) {
    return (keyByModuleId[name] === undefined && orderOfHotKey[0] === name) || force;
}

/**
 * @desc itemtab
 * @param item
 * @param name
 * @returns {boolean}
 */
function isAvailableFormItem(item, name) {
    let metaRes = availableFormItems.includes(item.itemtype) &&
        item.disabled !== true &&
        item.visible !== false;

    let itemDisabled = !!(formDisableAttrCode[name] &&
        formDisableAttrCode[name][item.attrcode] === true);
    return metaRes && !itemDisabled;
}

/**
 *
 * @param props
 * @param force {Boolean} 
 */
function keyMapOfTable(props, force = false) {
    let { name, high_hotkey, getHotKeyConfig } = props;

    // console.log(high_hotkey, getHotKeyConfig)
    let { withHotKeyChange, meta, orderOfHotKey: newOrderOfHotKey } = props.getHotKeyConfig ? props.getHotKeyConfig() : {};
    if (!props.getHotKeyConfig || !newOrderOfHotKey) {
        return console.warn('some form hotkey invalid');
    }
    orderOfHotKey = newOrderOfHotKey;
    let { store, tableId } = high_hotkey;

    let columns = meta[tableId] && meta[tableId].items;
    let rows = store.getData();
    let lastRowIndex = rows.length - 1;
    let availableItems = columns && iteratorTableColumns(tableId, columns, []);

    if (rows && rows.length === 0) return;
    processKeyMapOfTable({ tableId, availableItems, lastRowIndex, moduleType: MODULE_TYPE.cardTable, force });
    if (hasTabRelation(props, tableId) && keyByModuleId[tableId]) {
        createTabRelationKeyMap(props, tableId, lastRowIndex);
        //
        keyByModuleId[tableId].children = meta.gridrelation[tableId].tabRelation;
    }
}

function keyMapOfEditTable(props, force = false) {
    let { name, high_hotkey, getHotKeyConfig } = props;
    // console.log(high_hotkey, getHotKeyConfig)
    let { withHotKeyChange, meta, orderOfHotKey: newOrderOfHotKey } = props.getHotKeyConfig ? props.getHotKeyConfig() : {};
    if (!props.getHotKeyConfig || !newOrderOfHotKey) {
        return console.warn('some form hotkey invalid');
    }
    orderOfHotKey = newOrderOfHotKey;
    let { store, tableId } = high_hotkey;

    let columns = meta[tableId] && meta[tableId].items;
    let rows = store.getData();
    let lastRowIndex = rows.length - 1;
    let availableItems = columns && iteratorTableColumns(tableId, columns, []);

    if (rows && rows.length === 0) return;

    processKeyMapOfTable({ tableId, availableItems, lastRowIndex, moduleType: MODULE_TYPE.editTable, force });
}

function iteratorTableColumns(tableId, iterable, pool) {
    return iterable.reduce((pre, next) => {
        if (next.children) {
            iteratorTableColumns(tableId, next.children, pre);
        } else {
            isAvailableTableItem(tableId, next)
                ? pre.push(next.attrcode)
                : pre;
        }

        return pre;
    }, pool);
}

function isAvailableTableItem(moduleId, item) {
    //tableapimeta
    if (tableDisabledAttrCode[moduleId]) {
        if (tableDisabledAttrCode[moduleId][item.attrcode] === false) {
            return false;
        }

        if (tableDisabledAttrCode[moduleId][item.attrcode] === true) {
            return true;
        }
    }

    return availableTableItems.includes(item.itemtype) &&
        item.visible !== false &&
        item.disabled !== true &&
        item.attrcode !== undefined;
}

function processKeyMapOfTable({ moduleId, availableItems, lastRowIndex, moduleType, force }) {
    let firstItem = `${hotKeyPrefix}-${moduleId}-${availableItems[0]}-0`;
    let lastAvailableItem = availableItems.length - 1;
    let lastItem = `${hotKeyPrefix}-${moduleId}-${availableItems[lastAvailableItem]}-${lastRowIndex}`;

    publishFocusItem({ orderOfHotKey, moduleId, firstItem, force });

    keyByModuleId[moduleId] = {
        firstItem,
        lastItem,
        type: moduleType,
        activeTable: moduleId,
    };
}

/**
 * @desc    componentDidUpdate
 *          meta
 *          keyByModuleIdmoduleId
 *
 * @param orderOfHotKey
 * @param moduleId
 * @param firstItem
 * @param force executeAutoFocus api 
 */
function publishFocusItem({ orderOfHotKey, moduleId, firstItem, force }) {
    if (tableAutoFocusJudgement({ orderOfHotKey, moduleId, force })) {
        PubSub.publish('autoFocus', { data: firstItem, moduleId });
    }
}

function tableAutoFocusJudgement({ orderOfHotKey = [], moduleId, force }) {
    return (orderOfHotKey[0] === moduleId && keyByModuleId[moduleId] === undefined) || force;
}

/**
 * @desc tabletabRelation
 * @param props {object}
 * @param moduleId {string}
 */

function hasTabRelation(props, moduleId) {
    // console.log(high_hotkey, getHotKeyConfig)
    let { meta } = props.getHotKeyConfig ? props.getHotKeyConfig() : {};
    if (!props.getHotKeyConfig) {
        return console.warn('some form hotkey invalid');
    }

    let tabRelation = meta.gridrelation &&
        meta.gridrelation[moduleId].tabRelation;

    return !!(tabRelation && tabRelation.length > 1);
}

function createTabRelationKeyMap(props, moduleId, lastRowIndex) {
    let { meta } = props.getHotKeyConfig ? props.getHotKeyConfig() : {};
    if (!props.getHotKeyConfig) {
        return console.warn('some form hotkey invalid');
    }

    let tabRelation = meta.gridrelation[moduleId].tabRelation;

    tabRelation.forEach(item => {
        //keyMap
        if (item !== moduleId) {
            let columns = meta[item] &&
                meta[item].items;
            let availableItems = columns && iteratorTableColumns(moduleId, columns, []);
            let code = meta[item].code;

            let firstItem = `${hotKeyPrefix}-${moduleId}-${availableItems[0]}-0`;
            let lastAvailableItem = availableItems[availableItems.length - 1];

            let lastItem = `${hotKeyPrefix}-${moduleId}-${lastAvailableItem}-${lastRowIndex}`;
            /**
             * @type {{parent: string}} ID
             */
            keyByModuleId[code] = {
                firstItem,
                lastItem,
                type: MODULE_TYPE.cardTable,
                parent: moduleId,
            };
        }
    });
}

function deleteKeyByModuleId(moduleId) {
    delete keyByModuleId[moduleId];
}

function setFormDisableAttrCode(moduleId, values) {
    formDisableAttrCode[moduleId] = values;
}

/**
 *
 * @param disabledValue {boolean}  true ->  false -> 
 * @param moduleId  {string}
 * @param attrcode {string}
 */
function editTableFieldEditable(disabledValue, moduleId, attrcode) {
    if (tableDisabledAttrCode[moduleId] === undefined) {
        tableDisabledAttrCode[moduleId] = { [attrcode]: disabledValue };
    } else {
        tableDisabledAttrCode[moduleId][attrcode] = disabledValue;
    }
}

function getFirstItem(moduleId) {
    return keyByModuleId[moduleId] &&
        keyByModuleId[moduleId].firstItem;
}

function setValueOfActiveTable({ moduleId, activeKey }) {
    let moduleInfo = keyByModuleId[moduleId];
    if (moduleInfo) {
        moduleInfo.activeTable = activeKey;
    }
}

let createByModuleType = {
    form: keyMapOfForm,
    table: keyMapOfTable,
    editTable: keyMapOfEditTable,
};

export {
    createByModuleType,
    keyByModuleId,
    orderOfHotKey,
    deleteKeyByModuleId,
    setFormDisableAttrCode,
    editTableFieldEditable,
    getFirstItem,
    setValueOfActiveTable,
};