import React, { Component } from 'react';
import { HotKeys } from 'react-hotkeys';
import { keyMap } from './config/tableKeyboardMap';
import { createByModuleType, deleteKeyByModuleId } from './createKeyMap';
import { keyDownOfShiftUp, handleArrowKeyDown } from './handleKeyDown';
import { stopCardTableHotKeyChange } from './config/stopHotKeyChange';
import { findNextFocusItemInTable } from './util/findNextFocusItemInTable';

function TableWithKeyboardChange(WrappedComponent) {
    return class WithKeyboardChange extends Component {
        constructor(props) {
            super(props);
        }

        componentDidUpdate() {
            if (stopCardTableHotKeyChange(this.props)) { return; }

            createByModuleType['table'](this.props);
        }

        componentWillUnmount() {
            // KeyByModuleId   
            deleteKeyByModuleId(this.props.moduleId || this.props.name);
        }

        static tableStatusChanged({ props, status }) {
            if (status === 'edit' && !stopCardTableHotKeyChange(props)) {
                createByModuleType['table'](props);
            } else {
                deleteKeyByModuleId(props.moduleId);
            }

        }

        hotKeyWithEnter = ({ e, ViewModel, moduleId, target, itemType }) => {
            let eventTarget = target || e.target;
            let { orderOfHotKey } = this.props.getHotKeyConfig ? this.props.getHotKeyConfig() : {};
            if (!orderOfHotKey) {
                console.warn('not find orderOfHotKey in getHotKeyConfig')
                return;
            }

            //eventTargetdocumenttd,
            if (itemType === 'datePicker') {
                findNextFocusItemInTable({ eventTarget, ViewModel, moduleId, orderOfHotKey });
                return;
            }

            //CLOUD-147579 enterenter
            let timer = setTimeout(() => {
                findNextFocusItemInTable({ eventTarget, ViewModel, moduleId, orderOfHotKey });
                clearTimeout(timer);
            });
        };

        hotKeyWithArrow = ({ e, ViewModel, moduleId, itemType, target }) => {
            let eventTarget = target || e.target;
            let { orderOfHotKey } = this.props.getHotKeyConfig ? this.props.getHotKeyConfig() : {};
            if (!orderOfHotKey) {
                console.warn('not find orderOfHotKey in getHotKeyConfig')
                return;
            }

            handleArrowKeyDown({
                eventTarget,
                ViewModel,
                moduleId,
                orderOfHotKey,
                itemType,
                keyCode: e.keyCode,
            });
        };

        render() {
            return (
                <HotKeys
                    keyMap={keyMap}
                    handlers={{
                        shiftUp: keyDownOfShiftUp.bind(this, this.props.moduleId),
                        shiftDown: keyDownOfShiftUp.bind(this, this.props.moduleId),
                    }}
                >
                    <WrappedComponent
                        {...this.props}
                        tableStatusChanged={WithKeyboardChange.tableStatusChanged}
                        hotKeyWithEnter={this.hotKeyWithEnter}
                        hotKeyWithArrow={this.hotKeyWithArrow}
                    />
                </HotKeys>
            );
        }
    };
}

export default TableWithKeyboardChange;
